import Mock from 'mockjs' //mockjsnpm
import {
	Random
} from 'mockjs' // random,@
Mock.setup({
	timeout: 1 //
})
const ditu = function(option) { //
	let datalist = []
	for (let i = 0; i < 17; i += 1) {
		const o = { //mockjs7
			airMonitors: Random.string('number', 1), //
			companyid: Random.guid(12), //id
			companyName: Random.cword(9, 15), // 
			controlLevel: Random.county(), //
			latitude: Mock.mock({
				'latitude|32.8': 32.01,
			}),
			longitude: Mock.mock({
				'longitude|120.6': 120.8,
			}),
			shortName:Random.cword(3, 6),
			status:Random.string('number', 1), //
			waterMonitors:Random.string('number', 1), //
		}
		datalist.push(o)
	}
	return {
		data: datalist
	}
}
Mock.mock('/ditu', /post|get/i, ditu) //
