import Vue from 'vue'
import Vuex from 'vuex'
import {tcxa,tcxb} from './state'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		name: '',
		text: '',
		texta:'tcx',
		tcxa,
		tcxb,
	},
	mutations: {
	fangfa(state,val){
		this.state.tcxa = val
	},
    newname(state,str){
    	this.state.name = str
    },
    newtext(state,str){
    	this.state.text = str
    }
	},
	actions: {

	}
})
